import {
	JumooProcessingModalInfo,
	ProcessStepElement,
	ProcessStepElementBase,
} from '@jumoo/processing';
import { SyncPublisherStratey } from '../constants';
import { customElement, html, state } from '@umbraco-cms/backoffice/external/lit';
import { PublisherProcessingOptions } from '../../api';
import { PublisherTimeOptions } from '../../constants';

@customElement(SyncPublisherStratey.elements.backgroundScheduled)
export class SyncPublishBackgroundScheduledStepElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	@state()
	scheduledDate?: string;

	constructor() {
		super();
	}

	connectedCallback(): void {
		super.connectedCallback();

		const publisherOptions = this.options as PublisherProcessingOptions;

		if (!publisherOptions.custom?.releaseDate) return;
		this.scheduledDate = this.localize.date(
			publisherOptions.custom.releaseDate,
			PublisherTimeOptions,
		);

		this.dispatchEvent(
			new CustomEvent<JumooProcessingModalInfo>('pipeline-modal-update', {
				composed: true,
				bubbles: true,
				detail: {
					headline: `Scheduled for ${this.scheduledDate ?? ''}`,
					closeLabel: 'Close',
				},
			}),
		);
	}

	render() {
		return html`<div class="wrapper">
			<uui-box headline="Scheduled">
				<p>
					The process has been scheduled. and will start up again at the
					${this.scheduledDate ?? 'the specified time'}.
				</p>
			</uui-box>
		</div> `;
	}
}

export default SyncPublishBackgroundScheduledStepElement;
